<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-top">
        <div class="ct-left">
          <ctitle :title="'信访形式分布'"></ctitle>
          <div class="inner">
            <div class="count">
              <allCount class="all-count" :count="allcount"></allCount>
            </div>
            <div class="detail">
              <detailList :params="detailList"></detailList>
            </div>
            <div class="chart">
              <pie-chart3D :params="zbPieParams" @change="popShow"></pie-chart3D>
            </div>
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <ctitle :title="'四级登记机构-信访形式分布'"></ctitle>
        <div class="inner">
          <div class="chart" v-for="(item, index) of djjgArr" :key="index">
            <loopPie :params="item.params"></loopPie>
            <p class="label">{{ item.name }}</p>
          </div>
        </div>
      </div>
    </div>
    <!--数据反查弹框-->
    <popLists v-if="popLists" :data="popLists" ref="popLists"></popLists>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import popLists from "@/components/popLists/index";
  import ctitle from "@/components/title/index";
  import timeChange from "@/components/timeChange/index";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import LinesR from "@/components/line/LinesR";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";

  export default {
    name: "xfxs",
    props: {
      btnStatus: {
        type: String,
      }
    },
    components: {
      popLists,
      ctitle,
      timeChange,
      loopPie,
      PieChart3D,
      LinesR,
      allCount,
      detailList,
    },
    data() {
      return {
        popLists: null,
        // 信访形式占比
        allcount: -1,
        detailList: {},
        zbPieParams: {
          data: [],
          color: ["#655CDD", "#50B3FF", "#62EDAC"],
          angle: 20,
          radius: "40%",
          labelRadius: 15,
          styleObj: {
            // width: "100%",
            height: "100%"
          },
          hoverOffset: 15,
          isBr: true,
          labelPer: true
        },
        //四级登记机构
        djjgArr: [
          {name: "国家", params: {data: [], options: {}}},
          {name: "省级", params: {data: [], options: {}}},
          {name: "市级", params: {data: [], options: {}}},
          {name: "县级", params: {data: [], options: {}}}
        ],
        djjgPieParams: {
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["54%", "70%"],
            center: ["50%", "52%"],
            centerRadius: ["35%", "35%"],
            labelRich: {
              b: this.fontSize(0.16),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.4)
            },
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rollFlag: true
          }
        },
      };
    },
    mounted() {
      this.getAPI(this.date);
    },
    methods: {
      // 数据反查弹框
      popShow(obj) {
        let id = obj.id;
        // let id = obj.name;
        if (id) {
          get_http("popLists", {
            'value_id': id,
            'name': obj.name,
          })
          .then(res => {
            this.popLists = res.data;
            this.$nextTick(() => {
              this.$refs.popLists.popListsShow = true;
            })
          })
          .catch(err => {
            console.log(err);
          });
        }
      },
      getAPI(date) {
        // 1.信访形式占比（总数）
        get_http("qjywjkXfjjkXfxsXsfbTotal", {
          gd_id3: this.btnStatus,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.信访形式占比
        get_http("qjywjkXfjjkXfxsXsfbZb", {
          gd_id3: this.btnStatus,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.信访形式占比（饼图）
        get_http("qjywjkXfjjkXfxsXsfbPie", {
          gd_id3: this.btnStatus,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.zbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        this.getDjjgZbPieAPI(date);
      },
      // 4.四级登记机构-信访形式占比（饼图）
      // gd_id3=国家/省级/市级/县级   gd_id4=信访日期/登记日期/交换日期
      getDjjgZbPieAPI(date) {
        for (let item of this.djjgArr.values()) {
          item.params.options = this.djjgPieParams.options;
          get_http("qjywjkXfjjkXfxsSjdjjgPie", {
            day: `[${date.startDate},${date.endDate}]`,
            gd_id3: item.name,
            gd_id4: this.btnStatus
          })
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth =
            window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val);
      },
      btnStatus(val) {
        this.getAPI(this.date);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    top 0.8rem
    height calc(100% - 80px)
    .content
      height 100%
      display flex
      flex-direction column
      padding 0.2rem 0.3rem
      .c-top
        flex 2
        .ct-left
          .all-count
            width invert important
          .inner
            display flex
            justify-content space-around
            align-items center
            margin-left -0.5rem
            .count, .detail, .chart
              flex 1
            .count
              width 6rem
              margin-left 0.45rem
            .detail
              flex 1
              display flex
              justify-content center
              align-items center
              width 100%
              height 100%
            .chart
              height 3rem
              margin-right 1.1rem
      .c-bottom
        flex 3
        display flex
        flex-direction column
        .inner
          position relative
          z-index 1
          flex 1
          display flex
          align-items center
          justify-content space-around
          .label
            text-align center
            color #fff
            font-weight 700
            font-size 0.2rem
          .chart
            flex 1
            height 2.6rem
            margin-top -0.4rem
</style>
