<!--
  @auth:lcw
  @date:2020-9-15
  数据反查弹框
-->
<template>
  <transition name="fade">
    <div class="pop" v-if="popListsShow" @click.self="closePop">
      <div class="pop-wrapper" :style="{marginTop:popMarginTop+'px'}">
        <div class="inner">
          <ul class="list-ul list-title clearfloat">
            <li v-for="(item,index) of Object.values(data.map)" :key="index" :style="{width:columnW[index]+'px'}">
              {{item}}
            </li>
          </ul>
          <el-scrollbar ref="myScrollbar"
                        :style="{height:list.length>singlePageNum?liItemH*singlePageNum+'px':list.length*liItemH+'px'}">
            <div class="lists-container" :style="{height:data.length*liItemH+'px'}">
              <ul class="list-ul clearfloat" v-for="(item,index) of list" :key="index+'list'" v-if="list.length">
                <li
                  :class="{'number':i===0}"
                  v-for="(it,i) of Object.keys(data.map)"
                  :key="it"
                  :style="{width:columnW[i]+'px'}"
                  @click="handlePop($event,i,index,item[it])"
                >
                  {{item[it]}}
                </li>
              </ul>
              <transition name="fade">
                <div v-if="popShow" class="detail-pop" :style="{left:popX+'px',top:popY+'px'}">
                  <ul class="pop-ul clearfloat" v-if="detailData">
                    <li class="pop-li" v-for="(it,i) of Object.keys(detailData.map)" :key="i">
                      <p class="name">{{detailData.map[it]}} : </p>
                      <p class="value" :title="it.value">{{detailData.data[0][it]}}</p>
                    </li>
                  </ul>
                </div>
              </transition>
            </div>
          </el-scrollbar>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import {get_http} from "@/api/index";

  export default {
    name: "index",
    props: {
      data: {
        type: Object,
        default: {}
      }
    },
    data() {
      return {
        popListsShow: false,//大弹框显示/隐藏
        singlePageNum: 10,//单页行数
        popMarginTop: 0,  //顶部top值
        liItemH: 70,      //行高
        columnW: [210, 120, 120, 130, 210, 150, 130, 90, 110, 110, 110, 110], //列宽
        detailData: null, //小弹框数据
        popShow: false,   //小弹框显示/隐藏
        numberId: 0,      //信访件编号
        popX: 0,          //小弹框位置X
        popY: 0,          //小弹框位置Y
      }
    },
    mounted() {
      let dataLength = this.list.length > this.singlePageNum ? this.singlePageNum : this.list.length;
      this.popMarginTop = (1080 - (dataLength + 1) * this.liItemH - 56) / 2;
    },
    methods: {
      handlePop(e, i, index, number) {
        this.popX = e.clientX - 150;
        if (index < 3) {
          this.popY = e.clientY - this.popMarginTop + this.$refs['myScrollbar'].wrap.scrollTop - 50;
        } else {
          this.popY = e.clientY - this.popMarginTop + this.$refs['myScrollbar'].wrap.scrollTop - 300;
        }

        console.log(i);
        if (i === 0) {
          if (this.numberId === number) {
            this.popShow = false;
            this.numberId = 0;
          } else {
            this.popShow = true;
            this.numberId = number;
            this.getDetailAPI(this.numberId);
          }
        }
      },
      getDetailAPI(id) {
        get_http("popListsDetail", {xfjid: id})
          .then(res => {
            this.detailData = res.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      closePop(){
        this.popShow = false;
        this.popListsShow = false;
      }
    },
    computed: {
      list() {
        return this.data.data
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .fade-enter-active, .fade-leave-active
    transition opacity .3s

  .fade-enter, .fade-leave-to
    opacity 0

  .pop /deep/ .el-scrollbar__thumb
    background-color #29adff

  .pop
    position fixed
    top 0
    left 0
    z-index 100000
    width 1920px
    height 1080px
    background rgba(0, 0, 0, 0.6)

    .pop-wrapper
      position relative
      width 1602px
      margin 0 auto
      border-radius 10px
      overflow hidden
      border 1px solid #27a4f3
      box-shadow 0 0 20px rgba(0, 174, 255, 0.4) inset
      background url("./img/bg.png") no-repeat
      background-size 100% 100%
      .inner
        .lists-container
          position relative
          .detail-pop
            position absolute
            width 550px
            height 210px
            font-size 30px
            color #fff
            background url("./img/pop-bg.png") no-repeat
            background-size 100% 100%
            ul.pop-ul
              width 100%
              height 100%
              box-sizing border-box
              padding 20px 30px
              li.pop-li
                float left
                display flex
                width 50%
                font-size 0.14rem
                line-height 34px
                .name
                  width 84px
                  background linear-gradient(to bottom, #fff779, #ff8225)
                  -webkit-background-clip text
                  color transparent
                .value
                  width 170px
                  color #fff
                  overflow hidden
                  text-overflow ellipsis
                  white-space nowrap
        ul.list-ul
          &:nth-child(odd)
            background-color rgba(161, 151, 255, 0.1)

          &:nth-child(even)
            background-color transparent

          li
            box-sizing border-box
            border 1px solid #585a9e
            border-right none
            border-top none
            float left
            line-height 70px
            text-align center
            font-size 16px
            color #cbfbff
            overflow hidden
            text-overflow ellipsis
            white-space nowrap
            user-select none
            &.number
              cursor pointer
            &:last-child
              border-right 1px solid #130a3e

        ul.list-title
          background-color transparent !important
          li
            font-family '黑体'
            font-size 18px
            color #36edf7
</style>
